import React, { Component } from 'react';
import Notifier from '../components/Notifier';
import ClCamera from '../components/ClCamera';
import Header from "../components/Header/Header";

class CameraTestPage extends Component {
    constructor() {
        super();
        this.state = {
            offline: false,
            open: false
        }
    }

    componentDidMount() {
        window.addEventListener('online', () => {
            this.setState({ offline: false });
        });

        window.addEventListener('offline', () => {
            this.setState({ offline: true });
        });
    }

    componentDidUpdate() {
        let offlineStatus = !navigator.onLine;
        if (this.state.offline !== offlineStatus) {
            this.setState({ offline: offlineStatus });
        }
    }
    handleOpenNavBar = () => {
        this.setState({open:!this.state.open})
    }
    render() {
        return (
            <div >
                <div id="st-container" className={this.state.open?"st-container st-effect-1 st-menu-open":"wrapper dashboard-wrapper st-container"}>
                    <Header
                        header="Camera Sandbox"
                        openNavBar={this.handleOpenNavBar}
                        open={this.state.open} />
                    <div style={{textAlign: 'center'}}>
                        <Notifier offline={this.state.offline} />
                        <ClCamera offline={this.state.offline} />
                    </div>
                </div>
            </div>
        );
    }
}

export default CameraTestPage;
