import React , { Component } from "react";
import logoImg from '../../assets/images/logo.svg'
import MenuIcon from '../../assets/icons/menu-white.svg';
import {Link} from 'react-router-dom'

class Header extends Component {
    state = {
        open:false,
    }
    render() {
        return (

            <header>
                <div className="header-div header-top-root clearfix" >
                    <div className="inner-top-header-div clearfix">
                        <div className="container">
                            <div className="row">
                                <div className="col-lg-12 col-md-12">
                                    <div className="header-container">
        
                                        <div className="logo-div">
                                            <a className="logo_link clearfix" href="/">
                                                <img src={logoImg} className="img-fluid logo_img main-logo" alt="logo" />
                                            </a>
                                        </div>
        
                                        <div className="center-nav-content-div display-desktop-center" id="title-header-top">
                                            <div className="title-div">
                                                <h1>{this.props.header}</h1>
                                            </div>
                                        </div>
                                        
                                        <div className="nav-right-div">
                                            <div className="menu-trigger">
                                                <button className="btn btn-menu" id="btnopen-menu"  onClick={this.props.openNavBar}>
                                                    <img src={MenuIcon} className="menu-icon" alt="menu" />
                                                </button>
                                            </div>
                                                <div className="navigation-menu">
                                                    <nav className="st-menu st-effect-1 " id="menu-1">
                                                        <div className="closemenu-div">
                                                            <button className="menu-close-btn" id="closemenu" onClick={this.props.openNavBar}>
                                                                <i className="fe fe-x"> </i>
                                                            </button>
                                                        </div>
                                                        <ul className="ul-list">
                                                            <li>
                                                                <Link className="nav-link" to="/" >Dashboard</Link>
                                                            </li>
                                                            <li>
                                                                <Link className="nav-link" to="/inspection" >Brand Inspection/List</Link>
                                                            </li>
                                                            <li>
                                                            <Link className="nav-link" to="/livestock" > Live Stock</Link>
                                                            </li>
                                                            <li>
                                                                <Link className="nav-link" to="/horse-permanent-travel-permit" >Horse Permanent Travel Permit</Link>
                                                            </li>
                                                            <li>
                                                                <Link className="nav-link" to="/pre-sale-horse-permanent-travel-permit" >Pre-Sale Horse Brand Inspection Form</Link>
                                                            </li>
                                                            <li><Link className="nav-link" to='/counter'>Counter</Link></li>
                                                            <li><Link className="nav-link" to='/camera'>Camera</Link></li>
                                                            <li><Link className="nav-link" to='/gallery'>Gallery</Link></li>
                                                            <li><Link className="nav-link" to='/sketch'>Sketch</Link></li>
                                                            <li><Link className="nav-link" to='/bluetooth'>Bluetooth</Link></li>
                                                            <li><Link className="nav-link" to='/price-calculator'>Price Calculator</Link></li>
                                                        </ul>
                                                        
                                                    </nav>
                                            </div>
                                        </div>
        
                                    </div>
                                
                                </div>
                            </div>
                        </div>
                    </div>	
                </div>
        
                <div className="navigation-tabs-root" style={{display:"block"}}>
                    <ul className="nav nav-tabs">
                        <li className="nav-item plan-nav-item">
                            <a className="nav-link active" data-toggle="tab" href="#plan-tab"> <span className="round-outline plan-round-outline"></span> Plan</a>
                        </li>
                        <li className="nav-item produce-nav-item">
                            <a className="nav-link" data-toggle="tab" href="#produce-tab"> <span className="round-outline produce-round-outline"></span> Produce</a>
                        </li>
                        <li className="nav-item publish-nav-item">
                            <a className="nav-link" data-toggle="tab" href="#publish-tab"> <span className="round-outline publish-round-outline"></span> Publish</a>
                        </li>
                        <li className="nav-item track-nav-item">
                            <a className="nav-link" data-toggle="tab" href="#track-tab"> <span className="round-outline track-round-outline"></span> Track</a>
                        </li>
                    </ul>
                </div>
                            
            </header>
        ) ;
    }
}
export default Header